/* Reset */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

/* Body Content */
body {
  background-color: rgb(235, 233, 233);
  color: #222;
  display: flex;
  font-family: "Open Sans", sans-serif;
  justify-content: center;
  height: 100vh;
}

/* Header */
.header {
  background-color: #333;
  color: #eee;
}

.header nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.8rem 1.3rem;
}

.header nav h2 {
  font-size: 22px;
}

.header nav p {
  font-size: 13px;
  font-style: italic;
  text-align: center;
}

.main-wrapper {
  width: 100vw;
}

.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0 auto;
  padding: 2rem 0;
  max-width: 90%;
  width: 1100px;
}

/* Stories Section */
.stories-box {
  padding: 1.5rem 0;
  margin: 0 auto 2rem;
  width: 90%;
}

.single-story {
  background-color: rgb(244, 248, 245);
  font-family: "Roboto", sans-serif;
  margin-bottom: 1rem;
  padding: 1.2rem 0.8rem;
}

.single-story:last-of-type {
  margin-bottom: 0;
}

.story-headline {
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.story-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.story-details {
  font-size: 14px;
}

.story-info span {
  font-style: italic;
}

span.author-name {
  font-style: normal;
}

.story-cta {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.story-cta a {
  border: 1px solid rgb(21, 21, 129);
  border-radius: 2px;
  color: rgb(21, 21, 129);
  font-size: 13px;
  margin-right: 1rem;
  padding: 0.17rem 0.4rem;
}

.story-cta a:hover {
  background-color: rgb(21, 21, 129);
  color: #fff;
}

.hide-btn {
  background-color: transparent;
  border: 1px solid rgb(209, 33, 33);
  border-radius: 2px;
  color: rgb(209, 33, 33);
  cursor: pointer;
  font-size: 13px;
  padding: 0.2rem 0.4rem;
}

.hide-btn:focus {
  outline: none;
}

.hide-btn:hover {
  background-color: rgb(209, 33, 33);
  color: #fff;
}

/* Form Section */
form h2 {
  font-size: 18px;
  text-align: center;
  margin-bottom: 0.3rem;
}

.search-form {
  padding: 1rem 0;
  margin: 0 auto;
  width: 40%;
}

.search-input {
  background-color: transparent;
  border: transparent;
  border: 1px solid #555;
  letter-spacing: 1px;
  padding: 0.4rem 0.3rem;
  width: 100%;
}

/* Buttons */
.buttons-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  width: 15rem;
}

button:disabled {
  cursor: not-allowed;
}

.switch-btn {
  background-color: rgb(87, 87, 92);
  border: 1px solid rgb(87, 87, 92);
  border-radius: 2px;
  color: #fff;
  cursor: pointer;
  padding: 0.2rem 0.5rem;
}

.switch-btn:hover {
  opacity: 0.7;
}

.page-number {
  padding: 0 0.1rem;
}

/* Loading Animation */
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

.loading-box {
  margin-bottom: 3rem;
}

.loading {
  width: 5rem;
  height: 5rem;
  margin: 7rem auto 1.6rem;
  border-radius: 50%;
  border: 3px solid #ccc;
  border-top-color: #222;
  animation: spinner 0.6s linear infinite;
}

.error-box {
  margin: 3rem auto;
  text-align: center;
}

.error-box h2 {
  color: rgb(209, 33, 33);
  font-size: 18px;
  font-weight: 600;
}

.error-box p {
  font-size: 15px;
}

/* Mobile Responsiveness */
@media screen and (max-width: 650px) {
  .header nav h2 {
    font-size: 17px;
    margin-right: 0.5rem;
  }

  .header nav {
    padding: 0.8rem 0.5rem;
  }

  .header nav p {
    font-size: 10px;
  }

  .story-info {
    align-items: flex-start;
    flex-direction: column;
  }

  .story-cta a {
    padding: 0.2rem 0.4rem;
  }

  .story-details {
    margin-bottom: 0.5rem;
  }

  .search-form {
    width: 80%;
  }
}
